'use client';

import { useParams } from 'next/navigation';
import { useAccount } from 'wagmi';
import { useEffect } from 'react';
import { useRouter } from 'next/navigation';
import { useGameState } from '@/hooks/useGameState';
import { useTokenBalance } from '@/hooks/useTokenBalance';

export default function RoomPage() {
  const params = useParams();
  const router = useRouter();
  const { address, isConnected } = useAccount();
  const gameState = useGameState();
  const { apeBalance, bitBalance } = useTokenBalance();

  // Redirect if not connected or trying to access someone else's room
  useEffect(() => {
    if (!isConnected) {
      router.push('/');
    } else if (address && params?.address && 
      (typeof params.address === 'string') && 
      address.toLowerCase() !== params.address.toLowerCase()) {
      router.push(`/room/${address}`);
    }
  }, [isConnected, address, params?.address, router]);

  if (!isConnected || !address) {
    return null;
  }

  return (
    <div className="min-h-screen bg-royal p-6">
      <div className="max-w-7xl mx-auto">
        <h1 className="font-press-start text-2xl text-banana mb-8 pixel-text">
          MINING FACILITY: {address.slice(0, 6)}...{address.slice(-4)}
        </h1>

        {/* Resources Display */}
        <div className="grid grid-cols-1 md:grid-cols-2 gap-6 mb-8">
          <div className="bg-royal-blue p-6 rounded pixel-border">
            <h2 className="font-press-start text-banana mb-4">RESOURCES</h2>
            <div className="space-y-4">
              <div className="flex items-center gap-2">
                <span className="text-banana pixel-art">ðŸ’Ž</span>
                <span className="font-press-start">{apeBalance} APE</span>
              </div>
              <div className="flex items-center gap-2">
                <span className="text-banana pixel-art">ðŸª™</span>
                <span className="font-press-start">{bitBalance} BIT</span>
              </div>
            </div>
          </div>

          <div className="bg-royal-blue p-6 rounded pixel-border">
            <h2 className="font-press-start text-banana mb-4">MINING STATS</h2>
            <div className="space-y-4">
              <div className="flex items-center gap-2">
                <span className="text-banana pixel-art">âš¡</span>
                <span className="font-press-start">{gameState.hashRate} GH/s</span>
              </div>
              <div className="flex items-center gap-2">
                <span className="text-banana pixel-art">ðŸ“ˆ</span>
                <span className="font-press-start">{gameState.miningRate} BIT/day</span>
              </div>
            </div>
          </div>
        </div>

        {/* Mining Controls */}
        <div className="bg-royal-blue p-6 rounded pixel-border">
          <h2 className="font-press-start text-banana mb-4">MINING CONTROLS</h2>
          <div className="space-y-4">
            {!gameState.hasFacility ? (
              <button
                onClick={() => gameState.purchaseFacility()}
                disabled={gameState.isPurchasingFacility}
                className="w-full font-press-start py-3 px-6 bg-transparent border-2 border-banana text-banana hover:bg-banana hover:text-royal transition-colors pixel-button"
              >
                {gameState.isPurchasingFacility ? 'PURCHASING...' : 'PURCHASE FACILITY (10 APE)'}
              </button>
            ) : (
              <>
                <button
                  onClick={() => gameState.claimReward()}
                  disabled={gameState.isClaimingReward}
                  className="w-full font-press-start py-3 px-6 bg-transparent border-2 border-banana text-banana hover:bg-banana hover:text-royal transition-colors pixel-button"
                >
                  {gameState.isClaimingReward ? 'CLAIMING...' : 'CLAIM MINING REWARDS'}
                </button>
                <button
                  onClick={() => gameState.upgradeFacility()}
                  disabled={gameState.isUpgradingFacility}
                  className="w-full font-press-start py-3 px-6 bg-transparent border-2 border-banana text-banana hover:bg-banana hover:text-royal transition-colors pixel-button"
                >
                  {gameState.isUpgradingFacility ? 'UPGRADING...' : 'UPGRADE FACILITY'}
                </button>
              </>
            )}
          </div>
        </div>
      </div>
    </div>
  );
} 